export default function renderGallery(hits = []) {
  return hits
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => {
        return `
   <div class="photo-card">
     <a href="${largeImageURL}" class="link"> <img src="${webformatURL}" alt="${tags}" loading="lazy" width="250" height="150" /></a>
    <div class="info">
    <p class="info-item">
      <b>Likes: ${likes}</b>
    </p>
    <p class="info-item">
      <b>Views: ${views}</b>
    </p>
    <p class="info-item">
      <b>Comments: ${comments}</b>
    </p>
    <p class="info-item">
      <b>Downloads: ${downloads}</b>
    </p>
  </div>
</div>
`;
      }
    )
    .join('');
}
